.TrackPreview {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.TrackPreview button {
  padding: 0.4rem 0.75rem;
  background-color: #1db954;
  border-radius: 20px;
  font-size: 0.85rem;
  font-weight: bold;
  color: #fff;
  transition: background-color 0.2s ease;
}

.TrackPreview button:hover {
  background-color: #17a44b;
}

.TrackPreview audio {
  height: 30px;
  outline: none;
}
