.Track {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.75rem 1rem;
  border-radius: 8px;
  transition: background-color 0.2s ease;
}

.Track:nth-child(even) {
  background-color: #222;
}

.Track:nth-child(odd) {
  background-color: #2a2a2a;
}

.Track-info {
  display: flex;
  align-items: center;
  gap: 1rem;
  text-align: left;
}

.Track img {
  width: 50px;
  height: 50px;
  border-radius: 4px;
  object-fit: cover;
}

.Track h3 {
  margin: 0;
  font-size: 1rem;
}

.Track p {
  margin: 0;
  font-size: 0.85rem;
  color: #bbb;
}

.Track-action {
  font-size: 1.5rem;
  color: #1db954;
  cursor: pointer;
  padding: 0 0.5rem;
}

.Track-action:hover {
  color: #17a44b;
}
